.has-fade{
    visibility: hidden;
}
@keyframes fade-in{
    from {
        visibility: hidden;
        opacity: 0;
    }
    1%{
        visibility:hidden;
        opacity: 1;
    }
    to{
        visibility: visible;
        opacity: 1;
    }
}
.fade-in{
    animation:fade-in ease-in-out 300ms forwards ;
}
@keyframes fade-out {
    from{
        visibility: visible;
        opacity: 1;
    }
    99%{
        visibility: visible;
        opacity: 0;
    }
    to{
        visibility: hidden;
        opacity: 0;
    }
}
.fade-out{
    animation: fade-out 200ms forwards ease-in-out;
}